/* roboto-condensed-300 - latin_cyrillic */
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/roboto-condensed-english-cyrillic-300-normal.woff2")
      format("woff2"),
    url("../fonts/roboto-condensed-english-cyrillic-300-normal.woff")
      format("woff");
  font-display: swap;
}

